<template>
  <div>
    <h1 class="title">{{ info.title }}</h1>
    <div v-html="info.description"/>
  </div>
</template>
<script>
import { getAboutUs } from '@/api'
export default {
  props: {
    id: {
      type: [Number, String],
      default: () => ''
    }
  },
  data() {
    return {
      info: {}
    }
  },
  watch: {
    id: {
      handler() {
        this.getData()
      },
      immediate: true
    }
  },
  methods: {
    getData() {
      getAboutUs({id: this.id}).then(res => {
        this.info = res
      }).catch(() => {})
    }
  }
}
</script>
